
import { defineComponent, PropType } from "vue";
	
export interface StepperState {
	maxStep: number;
	currentStep: number;
}

export default defineComponent({
 	name: "LineStepper",
	props: {
		stepper: {
			type: Object as PropType<StepperState>,
			required: true,
		},
	},
	setup(props) {
		const isCompletedStep = (step: number) => (step <= props.stepper.currentStep);
		
		return {
			isCompletedStep
		};
	}
});
